.Navigation {
  position: absolute;
  height: 40px;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 10;
  background-color: rgba(200, 200, 200, 0.95);
  -webkit-box-shadow: 0 2px 20px 2px rgba(100, 100, 100, 0.5);
          box-shadow: 0 2px 20px 2px rgba(100, 100, 100, 0.5);
}

.Navigation a {
  vertical-align: middle;
  margin-top: 8px;
  font-size: 1.1em;
}


